import { Dialog, Grid, IconButton, Typography } from "@mui/material";
import React from "react";
import lowMemo1 from "../../assets/low_memo1.jpg";
import lowMemo2 from "../../assets/low_memo2.png";
import styled from "styled-components";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Autoplay } from "swiper";
import "swiper/css/bundle";
import "swiper/css";
import Close from "@mui/icons-material/Close";
const StyledImg = styled.img`
  height: auto;
  width: 100%;
`;

const StyledBox = styled.div`
  position: relative;
  padding: 16px;
  justify-content: center;
  align-items: center;
  display: flex;
`;

const StyledPaper = styled.div`
  padding: 16px;
  margin: 16px;
`;
const LowMemoryError = (props) => {
  const { open, setOpen } = props;
  SwiperCore.use([Autoplay]);
  return (
    <>
      <Dialog open={open} onClose={() => setOpen(false)}>
        <Grid container justifyContent={"end"}>
          <IconButton onClick={() => setOpen(false)}>
            <Close />
          </IconButton>
        </Grid>
        <StyledBox>
          <Swiper
            className="mySwiper"
            autoplay={{
              delay: 3000,
              disableOnInteraction: true,
            }}
          >
            <SwiperSlide>
              <Typography variant="subtitle1">Having trouble?</Typography>
              <Typography variant="body1">
                Apakah anda mengalami hal seperti gambar dibawah?
              </Typography>
              <StyledPaper>
                <StyledImg className="mySlides" src={lowMemo1} />
              </StyledPaper>
            </SwiperSlide>
            <SwiperSlide>
              <Typography variant="subtitle1">
                Pilih Galeri / Choose Gallery
              </Typography>
              <Typography variant="body1">
                Keluar dari browser anda, lalu foto dengan app kamera HP.
                Kembali ke browser dan pilih melalui galeri, lalu pilih foto
                anda
              </Typography>
              <Typography variant="body1">
                Exit from your browser, then took photo from your camera app,
                then back to the browser and choose gallery instead of camera,
                pick your photo.
              </Typography>
              <StyledImg className="mySlides" src={lowMemo2} />
            </SwiperSlide>
          </Swiper>
        </StyledBox>
      </Dialog>
    </>
  );
};

export default LowMemoryError;
