import { Grid, Paper, Box, Typography } from "@mui/material";
import makeStyles from "@mui/styles/makeStyles";
import React from "react";
import { ImgContribute } from "..";
import imager from "../../assets/bed.png";
import { NTRL_100, PRI_600 } from "../../theme/colors";
const useStyles = makeStyles(() => ({
  img: {
    width: "100%",
    height: "auto",
    objectFit: "contain",
    objectPosition: "center",
  },
  imgPaper: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    height: "auto",
    // maxHeight: "180px1"
  },
  box: {
    padding: "16px",
    backgroundColor: NTRL_100,
    borderRadius: "16px",
  },
  boldText: {
    color: PRI_600,
  },
  kelasPaper: {
    // display: "inline-block",
    padding: "6px",
  },
}));

const CardKamar = (props) => {
  const { ruang, kelas, kapasitas, terisi, kosong } = props;
  const classes = useStyles();
  return (
    <>
      <Box className={classes.box}>
        <Grid container spacing={2}>
          <Grid item container xs={12} spacing={2}>
            <Grid item xs>
              <Paper className={classes.kelasPaper}>
                <Typography
                  variant="subtitle2"
                  align="center"
                  className={classes.boldText}
                >
                  {ruang || ""}
                </Typography>
              </Paper>
            </Grid>
            <Grid item xs>
              <Paper className={classes.kelasPaper}>
                <Typography
                  variant="subtitle2"
                  align="center"
                  className={classes.boldText}
                >
                  {kelas || ""}
                </Typography>
              </Paper>
            </Grid>
          </Grid>
          <Grid item xs={12}>
            <Paper className={classes.imgPaper}>
              <ImgContribute
                type="small"
                title="Illustrations by Storyset"
                src={imager}
                alt="Available Room"
                link="https://storyset.com"
              />

              {/* <img src={imager} className={classes.img} alt="card" />
              <Typography variant="body2">Illustrations by Storyset</Typography> */}
            </Paper>
          </Grid>
          <Grid item xs={12}>
            <Paper>
              <Grid container>
                <Grid item xs>
                  <Typography align="center" variant="body1">
                    Kapasitas
                  </Typography>
                  <Typography
                    align="center"
                    variant="h5"
                    className={classes.boldText}
                  >
                    {kapasitas || ""}
                  </Typography>
                </Grid>
                <Grid item xs>
                  <Typography align="center" variant="body1">
                    Terisi
                  </Typography>
                  <Typography
                    align="center"
                    variant="h5"
                    className={classes.boldText}
                  >
                    {terisi || ""}
                  </Typography>
                </Grid>
                <Grid item xs>
                  <Typography align="center" variant="body1">
                    Kosong
                  </Typography>
                  <Typography
                    align="center"
                    variant="h5"
                    className={classes.boldText}
                  >
                    {kosong || ""}
                  </Typography>
                </Grid>
              </Grid>
            </Paper>
          </Grid>
        </Grid>
      </Box>
    </>
  );
};

export default CardKamar;
