import React from "react";
import ErrorDialog from "./ErrorDialog";

function CustomDialog(props) {
  const { type } = props;
  let html = "";
  switch (type) {
    case "error":
     html= <ErrorDialog {...props} />;
      break;
    default:
      html = <ErrorDialog {...props} />;
      break;
  }
  return <>{html}</>;
}

export default CustomDialog;
