import React from "react";
import styled from "styled-components";
import { NTRL_200, PRI_500 } from "../../theme/colors";
const SelectedChip = styled.div`
  width: auto;
  padding: 8px 16px;
  color: ${NTRL_200};
  font-size: 10px;
  border-radius: 8px;
  background-color: ${PRI_500};
`;
const DefaultChip = styled.div`
  width: auto;
  padding: 8px 16px;
  color: ${PRI_500};
  border-color: ${PRI_500};
  border: 1px solid;
  font-size: 10px;
  border-radius: 8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

const CustomChip = (props) => {
  const { variant, label } = props;
  let html = "";
  switch (variant) {
    case "selected":
      html = <SelectedChip>{label}</SelectedChip>;
      break;

    default:
      html = <DefaultChip>{label}</DefaultChip>;

      break;
  }
  return <>{html}</>;
};

export default CustomChip;
