import React, { useRef } from "react";
import jsbarcode from "jsbarcode";
import styled from "styled-components";
import { Button, Stack, Typography } from "@mui/material";
const CustomCanvas = styled.canvas`
    width: auto;
    height: 150px;
`
const Barcode = (props) => {
  const { barcode, action } = props;
  let ref = useRef(null);
  const [disabled, setDisabled] = React.useState(true);
  React.useEffect(() => {
    jsbarcode(ref.current, barcode);
      setTimeout(() => {
        setDisabled(false);
      }, 2000);

  }, [barcode]);
  const save = () => {
    var gh = ref.current.toDataURL("image/png");
    var a = document.createElement("a");
    a.href = gh;
    a.download = "image.png";
    a.click();
    action && action();
  };
  return (
    <Stack spacing={2}>
      <Typography variant="body1" align="center">
        Mohon simpan barcode kedalam galeri anda, barcode akan digunakan saat daftar
        ulang di RS
      </Typography>
      <CustomCanvas id="barcode" ref={ref} />
      <Button variant="contained" onClick={save} disabled={disabled}>
        Simpan dan Tutup
      </Button>
    </Stack>
  );
};

export default Barcode;
