import React from "react";
import TotalPayment from "./TotalPayment";
const BottomPayment = (props) => {
  let html = "";
  let { type } = props;
  switch (type) {
    case "total_payment":
      html = <TotalPayment {...props}/>;
      break;
    default:
      html = <TotalPayment {...props} />;
      break;
  }
  return <>{html}</>;
};

export default BottomPayment;
