import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Pagination } from "swiper";
import "swiper/css/bundle";
import "swiper/css";
import "./style.css";
import makeStyles from "@mui/styles/makeStyles";
import Typography from "@mui/material/Typography";
import { ImgContribute } from "..";
const SmallCarousel = (props) => {
  const { carousel, type } = props;
  SwiperCore.use([Pagination]);

  const useStyles = makeStyles({
    small: {
      height: "300px",
      width: "100%",
    },
    big: {
      height: "auto",
      width: "100%",
    },
    sliderContainer: {
      marginBottom: "32px",
    },
    smallImg: {
      width: "auto",
      maxHeight: "300px",
      objectFit: "cover",
    },
    bigImg: {
      width: window.innerWidth <= 500 ? `${window.innerWidth - 60}px` : "auto",
      // width: `${window.innerWidth - 60}px`,
      maxHeight: "500px",
      // objectFit: "cover",
    },
  });
  const classes = useStyles();
  const Small = () => {
    return (
      <div className={classes.small}>
        <Swiper pagination={true} className="mySwiper">
          {carousel &&
            carousel.map((item, index) => {
              return (
                <SwiperSlide key={"carousel_" + index}>
                  <ImgContribute
                    title="Illustrations by Storyset"
                    src={item.image}
                    alt="Contribute"
                    link="https://storyset.com"
                  />

                  {/* <img
                    src={item.image}
                    className={classes.smallImg}
                  /> */}
                </SwiperSlide>
              );
            })}
        </Swiper>
      </div>
    );
  };
  const Big = () => {
    return (
      <div className={classes.big}>
        <Swiper pagination={true} className="mySwiper">
          {carousel &&
            carousel.map((item, index) => {
              return (
                <SwiperSlide key={"carousel_" + index}>
                  <div className={classes.sliderContainer}>
                    <ImgContribute
                      title="Illustrations by Storyset"
                      src={item.image}
                      alt="Contribute"
                      link="https://storyset.com/"
                    />
                    {item.title && (
                      <Typography variant="subtitle1" align="center">
                        {item.title}
                      </Typography>
                    )}
                    {item.text && (
                      <Typography variant="body1" align="center">
                        {item.text}
                      </Typography>
                    )}
                  </div>
                </SwiperSlide>
              );
            })}
        </Swiper>
      </div>
    );
  };
  let component = null;
  switch (type) {
    case "small":
      component = <Small />;
      break;
    case "big":
      component = <Big />;
      break;
    default:
      component = <Small />;
      break;
  }
  return <>{component}</>;
};

SmallCarousel.propTypes = {};

export default SmallCarousel;
