import Close from "@mui/icons-material/Close";
import { Button, Container, Dialog, Grid, IconButton, Typography } from "@mui/material";
import makeStyles from "@mui/styles/makeStyles";
import React from "react";
import failImg from "../../../assets/hand_fail.png";
import succImg from "../../../assets/hand_succ.png";
const useStyles = makeStyles(() => ({
  img: {
    width: "auto",
    maxHeight: "150px",
  },
  container:{
      padding: "16px 0px"
  }
}));
const ErrorDialog = (props) => {
  const { title, setOpen, open, type } = props;
  const classes = useStyles();
  const handleClose = () => {
    return setOpen(false);
  };
  return (
    <>
      <Dialog open={open} onClose={handleClose}>
        <Container className={classes.container}>
          <Grid container spacing={2} justifyContent={"center"}>
            <Grid item container justifyContent={"end"} xs={12}>
              <IconButton onClick={handleClose}>
                <Close />
              </IconButton>
            </Grid>
            <Grid item xs={12} container justifyContent={"center"}>
              <img
                src={type === "error" ? failImg : succImg}
                className={classes.img}
                alt="failed"
              />
            </Grid>
            <Grid item xs={12}>
              <Typography align="center" variant="body1">
                {title}
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Button variant="contained" onClick={handleClose} fullWidth>
                Close
              </Button>
            </Grid>
          </Grid>
        </Container>
      </Dialog>
    </>
  );
};

export default ErrorDialog;
