import React from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import makeStyles from "@mui/styles/makeStyles";
import imageCompression from "browser-image-compression";
import { CircularProgress } from "@mui/material";
import { PRI_600 } from "../../theme/colors";

const useStyles = makeStyles({
  image: {
    maxHeight: "220px",
    width: "auto",
    objectFit: "contain !important",
  },
  cardRoot: {
    paddingTop: "16px",
  },
});

const ImageInput = (props) => {

  const {
    // eslint-disable-next-line no-unused-vars
    default_value,
    // eslint-disable-next-line no-unused-vars
    is_readonly,
    is_required,
    // eslint-disable-next-line no-unused-vars
    is_error,
    value,
    handleChange,
    field_name,
  } = props;
  const classes = useStyles();

  const [size, setSize] = React.useState("");
  const [loading, setLoading] = React.useState(false);
  const _change = async (event) => {
    let file = event.target.files[0];
    setLoading(true);

    function blobToBase64(blob) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(blob);
        reader.onload = () => resolve(reader.result);
        reader.onerror = (error) => {
          reject(error);
        };
        return reader;
      });
    }

    if (file) {
      const options = {
        maxSizeMB: 0.5,
        useWebWorker: true,
      };
      try {
        const compressedFile = await imageCompression(file, options);
        return blobToBase64(compressedFile)
          .then((data) => {
            handleChange({
              target: {
                id: field_name,
                value: data,
              },
            });
          })
          .catch((err) => {
            alert(err.toString());
            setSize("Please Re-upload!, Coba gunakan galeri bukan kamera.");
          });
        // await uploadToServer(compressedFile); // write your own logic
      } catch (error) {
        // console.log(error);
        setSize("Please Re-upload!, Coba gunakan galeri bukan kamera.");
      } finally {
        setLoading(false);
      }
      // const getBase64 = async () => {
      //   const result = await toBase64(file);
      //   // setImage(result);

      //   handleChange({
      //     target: {
      //       id: parseToString(form_child_id),
      //       value: result,
      //       is_error: is_required === 1 && !result ? true : false,
      //     },
      //   });
      // };
      // getBase64();
    }
  };
  return (
    <Card className={classes.cardRoot}>
      {loading && (
        <CircularProgress
          sx={{
            color: PRI_600 + "!important",
          }}
        />
      )}
      {value && (
        <CardMedia
          component="img"
          // height="auto"
          // width="100%"
          className={classes.image}
          image={value}
          alt={field_name}
        />
      )}
      <CardContent>
        <Typography variant="subtitle2" color="text.secondary">
          {size}
        </Typography>
        <Typography variant="body2" color="text.secondary">
          {field_name}
          {is_required === 1 ? "*" : ""}
        </Typography>
        <input
          required={is_required === 1}
          type="file"
          accept="image/*"
          // fullWidth
          onChange={_change}
          // error={is_error}
        />
      </CardContent>
    </Card>
  );
};

export default ImageInput;
