import Paper from "@mui/material/Paper";
import Typography from "@mui/material/Typography";
import React from "react";
import makeStyles from "@mui/styles/makeStyles";
import promo from "../../../assets/bg_two.jpg";

const useStyles = makeStyles(() => ({
  paper: {
    cursor: "pointer",
    width: "100%",
    height: "110px",
    overflow: "hidden",
    // backgroundColor: "#FBD22F !important",
    backgroundImage: `url(${promo})`,
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    backgroundSize: "cover",
  },
  image: {
    width: "100%",
    height: "110px",
    objectFit: "cover",
    objectPosition: "center",
  },
}));
const Promo = (props) => {
  const classes = useStyles();
  const { title, subtitle, onclick } = props;
  return (
    <Paper className={classes.paper} onClick={onclick}>
      <div>
        <Typography variant="subtitle2" color="white">
          {title}
        </Typography>
        <Typography variant="subtitle1" color="white">
          {subtitle}
        </Typography>
      </div>
      {/* <img src={promo} className={classes.image} alt="promo"/> */}
    </Paper>
  );
};

export default Promo;
